<template>
	<div class="case-box count-shade">
    <h1></h1>
		<p class="mt30"> npm install gsap</p>
		<p class="mt10">GSAP2D</p>
		<div class="scene">	GSAP</div>
		<ul>
			<li>1GSAPcss</li>
			<li>2TweenLiteGSAPTweenLite, TimelineLite, TimelineMax  TweenMax</li>
			<li>3</li>
		</ul>
		<div class="solve"></div>
		<div class="number mb30">{{changeNumber}}</div>
		<el-button type="primary" @click="DigitalLoad(3567)" v-text="text"></el-button>

		<div id="demo">
			<div class="box green" id="box1"></div>
		</div>
		<el-button size="mini" :underline="false" @click="handle(0)">play() </el-button>
		<el-button size="mini" :underline="false" value="pause()" @click="handle(1)">pause() </el-button>
		<el-button type="primary" size="mini" :underline="false" @click="handle(2)">resume() </el-button>
		<el-button type="primary" size="mini" value="reverseBtn()" :underline="false" @click="handle(3)">reverse() </el-button>
		<el-button plain size="mini" :underline="false" @click="timeScale(0.5)">timeScale(0.5) </el-button>
		<el-button type="primary" size="mini" :underline="false" @click="timeScale(1)">timeScale(1) </el-button>
		<el-button type="primary" size="mini" :underline="false" @click="timeScale(2)">timeScale(2) </el-button>
		<el-button type="primary" size="mini" :underline="false" @click="restart">restart </el-button>
	</div>
</template>

<script>
import { TweenMax,TimelineMax, Power2, TimelineLite } from "gsap"

export default {
	name: 'count-shade',
	computed: {
    changeNumber() {
      return this.tweenedNumber.toFixed(0);
    }
  },
	data() {
		return {
			text: '1->100 ',
			time: 0.5, // 
			tweenedNumber: 0, // 0
      NumberValue: 3567, // 
      NumberShow: false // 
		}
	},
	methods: {
		// 
    DigitalLoad(value) {
			// 
			let TweenLite = new TimelineLite()
			this.NumberShow = !this.NumberShow

      if (this.NumberShow) {
				this.text = '100->1 '
        TweenLite.to(this.$data, this.time, { tweenedNumber: value })
      } else {
				this.text = '1->100 '
        TweenLite.to(this.$data, this.time, { tweenedNumber: 0 })
      }
		},
		// 
		handle (status) {
			// 
			let tm = new TimelineLite()
			// let tm = new TimelineMax()
			// tm.to('#box1', 6, {x: 632})
      if (status === 0) {
				tm.play()
			}
			if (status === 1) {
				// tween.pause()
				// let tm = new TimelineMax();
        // tm.to('#box1', 6, {x: 500});

				// alert(1)
				tm.pause()
			}
			if (status === 2) {
				tm.resume()
				return
			}
			if (status === 3) {
				// tween.reverse()
				// reverseBtn.onclick = function() {
				// 		tween.reverse();
				// }
			}
		},
		// 
		timeScale (time) {
			// 
			let TweenLite = new TimelineLite()
			let tween = TweenLite.to(box1, 6, {left:"632px"})
			if (time === 0.5) {
 				tween.timeScale(0.5)
			}
			if (time === 1) {
 				tween.timeScale(1)
			}
			if (time === 2) {
 				tween.timeScale(2)
			}
		},
		// 
		restart () {
			// 
			let TweenLite = new TimelineLite()
			let tween = TweenLite.to(box1, 6, {left:"632px"})
			tween.restart()
		}
	}
}
</script>

<style lang="less" scoped>
.count-shade {
	.number {
		color: #e6a23c;
		font-size: 60px;
		font-family: fantasy;
	}
	#demo {
		width: 692px;
		height: 60px;
		background-color: #333;
		padding: 8px;
		margin-bottom: 10px;
	}
	.box {
		width: 50px;
		height: 50px;
		border-radius: 6px;
		margin-top: 4px;
		display: inline-block;
		position: absolute;
	}
	.green {
		background-color: #6fb936;
	}
}
</style>
